.questions-cont {
    display: flex;
    margin: auto;
}
.question-detail {
    margin-right: 10px;
}
.question-header {
    display: flex;
}
.question-detail .el-btn {
    margin: 0 5px 0 0;
    padding: 3px 4px;
}
.question-detail .el-btn > svg {
    width: 15px;
    height: 17px;
}
.question-quotes {
    text-align: right;
}
.question-type {
    text-transform: capitalize;
}
.questions-cont h2 {
    margin: 10px 0 0 15px;
}
.questions-cont img {
    margin-left: 10px;
    max-width: 175px;
}
.questions-cont p {
    margin: 10px 0 10px 17px;
}
.el-option {
    width: fit-content;
    margin: 10px 0 20px 0;
}
.el-option button {
    padding: 15px;
}
.score {
    margin: 40px 0 40px 20px;
}
.correct, .wrong {
    font-size: 2em;
}
.correct {
    color: #008000;
}
.wrong {
    color: #800000;
}
.el-blank {
    font-family: arial;
    margin-right: 5px;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
    user-select: none;
}
.order-question {
    display: flex;
    flex-wrap: wrap;
    min-height: 48px;
    font-size: 1.8em;
    margin-bottom: 30px;
}
.order-question > .el-btn {
    margin: 0 16px 5px 0;
}
.matching-question {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: minmax(50px, 1fr) 1fr 1fr;
    padding-top: 10px;
    gap: 2em;
}
.matching-question .el-option,
.matching-question .el-option button {
    width: 100%;
    height: 100%;
    margin: 0;
}
.matching-question .el-option button {
    transition: box-shadow 100ms linear;
    padding: 0;
}
.matching-question .el-option button img {
    margin: 0;
}
.rs-play:disabled {
    cursor: default;
    opacity: 0.5;
}
.el-option.selected button {
    box-shadow: 0 0 10px 5px #eeee00;
}
.el-option.true button {
    box-shadow: 0 0 10px 5px #008000;
}
.el-option.false button {
    box-shadow: 0 0 10px 5px #800000;
}